import React from "react";
import { Line } from "react-chartjs-2";

const initialState = {
  labels: [
    "August 2018",
    "September 2018",
    "October 2018",
    "November 2018",
    "December 2018",
    "January 2019",
    "February 2019",
    "March 2019",
    "April 2019",
    "May 2019",
    "June 2019",
    "July 2019"
  ],
  datasets: [
    {
      label: "2019 Message Count",
      fill: false,
      lineTension: 0.2,
      backgroundColor: "#264160",
      borderColor: "#264160",
      pointBorderColor: "#EBCC60",
      pointBackgroundColor: "#EBCC60",
      pointHoverBackgroundColor: "#EBCC60",
      pointHoverBorderColor: "#EBCC60",
      pointHoverBorderWidth: 2,
      pointRadius: 1,
      pointHitRadius: 10,
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      data: [65, 59, 80, 81, 56, 55, 40, 26, 45, 78, 65, 23]
    }
  ]
};

export default class LineGraph extends React.Component {
  componentWillMount() {
    this.setState(initialState);
  }

  render() {
    console.log(this.props);
    var chartData = {
      labels: this.props.labels,
      datasets: [
        {
          label: "2019 Message Count",
          fill: false,
          lineTension: 0.2,
          backgroundColor: "#264160",
          borderColor: "#264160",
          pointBorderColor: "#EBCC60",
          pointBackgroundColor: "#EBCC60",
          pointHoverBackgroundColor: "#EBCC60",
          pointHoverBorderColor: "#EBCC60",
          pointHoverBorderWidth: 2,
          pointRadius: 1,
          pointHitRadius: 10,
          pointBorderWidth: 1,
          pointHoverRadius: 5,
          data: this.props.data
        }
      ]
    };
    return (
      <div>
        <Line data={chartData} />
      </div>
    );
  }
}
